@include media-breakpoint-up(lg) {
	.framed {
		position: relative;
		&::before {
			position: absolute;
			content: "";
			width: 4*$spacer;
			height: 4*$spacer;
			top: -2*$spacer;
			left: -2*$spacer;
			border-top:  0.4*$spacer solid $utorange;
			border-left: 0.4*$spacer solid $utorange;
		}
